function sliderContests() {
    var swiper = new Swiper('.contests .swiper', {
        slidesPerView: '3',
        spaceBetween: 24,
        loop: true,
        pagination: {
            el: ".contests .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 1.8,
            },
            993: {
                slidesPerView: 3,
            },
        }
    })
}

function sliderVacancies() {
    var swiper = new Swiper('.vacancies .swiper', {
        slidesPerView: '3',
        spaceBetween: 24,
        navigation: {
            nextEl: ".vacancies .swiper-button-next",
            prevEl: ".vacancies .swiper-button-prev",
        },
        pagination: {
            el: ".vacancies .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            },
        }
    })
}


$(document).ready(function() {

    sliderContests();
    sliderVacancies();

    //Burger
    $('.burger').click(function () {
        $('.header__right').toggleClass('header__right--active')
    })

    $('.close').click(function () {
        $('.header__right').removeClass('header__right--active')
    })

    let burger = $('.burger')
    let headerRight = $('.header__right')

    $(document).click(function (e) {
        if ( !burger.is(e.target) && burger.has(e.target).length === 0 &&
            !headerRight.is(e.target) && headerRight.has(e.target).length === 0

        ) {
            headerRight.removeClass('header__right--active')
        }
    })

    // Hover-Click menu
    $(".submenu").hover(onIn, onOut);
    $(".submenu .submenu-show").click(function() {
        if ($(this).parents(".submenu").hasClass("submenu--active")) {
            $(this).parents(".submenu").removeClass("submenu--active")
        } else {
            $(this).parents(".submenu").addClass("submenu--active")
        }
    })

    //Input
    $(document).on('input', function () {

        if ($('.search-form__input').val()) {
            $('#placeholderDiv').hide();
        } else {
            $('#placeholderDiv').show();
        }

    });

    //Accordion
    $(".questions__item-show").click(function () {
        let parent = $(this).parents(".questions__item")
        if($(parent).hasClass("questions__item--active")){
            $(".questions__item").removeClass("questions__item--active")
            $(".questions__item-hidden").slideUp()
        }else{
            $(".questions__item").removeClass("questions__item--active")
            $(parent).addClass("questions__item--active")
            $(".questions__item-hidden").slideUp()
            $(this).siblings(".questions__item-hidden").slideDown()
        }
    })

    //Input-file
    $('#file').on('change', function (e) {
        $(this).prev('span').html(e.target.files[0].name);
    });

})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("submenu--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("submenu--active")
    }
}